<template>
  <div class="order">
    <div class="title">我的订单</div>
    <div class="content">
      <template  v-for="(item, index) in orders">
        <div class="order" :key="index">
          <div class="header">
            <div class="number">订单号：{{item.order_no}}</div>
            <div class="time">订单时间: {{item.create_time}}</div>
            <div class="status">支付状态:
              <span v-if="item.order_status == -1" class="color-cancel">已取消</span>
              <span v-if="item.order_status == 0" class="color-suspend">待支付</span>
              <span v-if="item.order_status == 1" class="color-success">已支付</span>
            </div>
            <div class="money">订单金额: <span>￥{{item.money}}</span></div>
          </div>
          <div class="container">
            <div class="list">
              <div class="cell" v-for="(game, index2) in item.games" :key="index2">
                <div class="left">
                  <img class="pic" :src="game.game_image" />
                </div>
                <div class="center">
                  <div class="info">
                      <span v-if="game.rdm_id == 1" class="rdm"><img class="icon" src="@/assets/img/Steam.png" alt="" /></span>
                      <span v-if="game.rdm_id == 2" class="rdm"><img class="icon" src="@/assets/img/Epic.png" alt="" /></span>
                      <span v-if="game.rdm_id == 3" class="rdm"><img class="icon" src="@/assets/img/Uplay.png" alt="" /></span>
                      <span v-if="game.rdm_id == 4" class="rdm"><img class="icon" src="@/assets/img/Origin.png" alt="" /></span>
                      <span v-if="game.rdm_id == 5" class="rdm"><img class="icon" src="@/assets/img/Rockstar.png" alt="" /></span>
                      <span class="district">{{game.district}}</span>
                      <svg-icon v-if="game.good_type==1" size="20" icon-class="cdkey" />
                      <span v-if="game.good_type==1" class="cdkey">CDKEY</span>
                      <svg-icon v-if="game.good_type==2" size="20" icon-class="activtiy" />
                      <span v-if="game.good_type==2" class="cdkey">礼物</span>
                  </div>
                  <div>{{game.game_cname}} - {{game.version_cname}}</div>
                  <div class="sys">
                      <span v-if="game.win" class="platform"><svg-icon size="22" icon-class="windows" /></span>
                      <span v-if="game.mac" class="platform"><svg-icon size="22" icon-class="mac" /></span>
                      <span v-if="game.linux" class="platform"><svg-icon size="22" icon-class="linux" /></span>
                  </div>
                </div>
                <div class="right" v-if="item.order_status > 0 && game.good_type == 1">
                    <div class="show">
                      <input class="cdkeycon" type="text" :value="game.show_cdkey"  disabled/>
                      <span class="redeem" @click="showCdkey(game)" :data-clipboard-text="game.show_cdkey">{{game.redeem_txt}}</span>
                    </div>
                    <a class="howto" v-if="(game.status > 0) && (game.howto > 0)" @click="howto()">如何兑换 CDKEY ?</a>
                </div>
                <div class="right" v-if="item.order_status > 0 && game.good_type == 2">
                    <div class="show">
                      <input class="giftcon" type="text" v-model="game.user_steam_id" placeholder="请输入您的 Steam ID" :disabled="game.status == 2"/><!--game.steam_friend_relation == 1 ||--> 
                      <span class="gift" @click="addFriend(game)" v-if="(game.status == 1 && game.steam_friend_relation == 0)">添加好友</span>
                      <span class="gift" @click="getGift(game)" v-if="(game.status == 1 && game.steam_friend_relation == 1)">领取礼物</span>
                      <span class="gift" v-if="(game.status == 2)">已发放</span>
                    </div>
                    <a class="howto" v-if="(game.status > 0) && (game.howto > 0)" @click="howto()" >如何查看 Steam ID ?</a>
                </div>
              </div>
            </div>
            <div v-if="item.order_status == 0" class="toPay">
              <div>
                <Button class="alipay" type="info"  style="width: 130px;height: 48px;border-radius: 5px;margin-bottom: 20px;" @click="rePay(item)"><span>去支付</span></Button>
              </div>
              <div>
                <Button class="cancel" type="error"  style="width: 130px;height: 48px;border-radius: 5px;" @click="cancelOrder(item.id)"><span>取消订单</span></Button>
              </div>
            </div>
            <div v-if="item.order_status < 0" style="width:100px">
                <span class="color-cancel">订单已取消</span>
            </div>
          </div>
        </div>
      </template>
      <div v-if="orders.length == 0">您还未购买游戏~ <a @click="toHome()" class="toHome">点我去购买</a></div>
    </div>
    <!-- 重新发起支付数据表单 -->
    <form id="payForm" name="payForm" method="post" action="https://gateway.xddpay.com" style="display:none">
      <input name='order_no' id='order_no' type='text' v-model="payFormPayData.order_no"/>
      <input name='subject' id='subject' type='text' v-model="payFormPayData.subject" />
      <input name='pay_type' id='pay_type' type='text' v-model="payFormPayData.pay_type" />
      <input name='money' id='money' type='text' v-model="payFormPayData.money"/>
      <input name='app_id' id='app_id' type='text' v-model="payFormPayData.app_id"/>
      <input name='extra' id='extra' type='text' v-model="payFormPayData.extra"/>
      <input name='sign' id='sign' type='text' v-model="payFormPayData.sign"/>
      <input type='submit' id='submitPayForm'>
    </form>

    <!-- howto -->
    <Modal v-model="howto_modal" width="720" :footer-hide="false">
        <div style="color:#000">
          <Timeline>
            <TimelineItem color="green">
              <p>1.启动Steam客户端并登录您的帐号</p>
              <img src="@/assets/img/how_check_steamid_step1.png" style="width:600px;margin:10px 0 0 0"/>
            </TimelineItem>
            <TimelineItem color="green">
              2.点击右上角帐户名弹出下拉菜单，再点击帐户明细
              <img src="@/assets/img/how_check_steamid_step2.png" style="width:600px;margin:10px 0 0 0"/>
            </TimelineItem>
            <TimelineItem color="green">
              3.点击左侧菜单的帐户明细（默认展示），上方显示的17位数字便是您的 SteamID
              <img src="@/assets/img/how_check_steamid_step3.png" style="width:600px;margin:10px 0 0 0"/>
            </TimelineItem>
          </Timeline>
        </div>
    </Modal>
  </div>
</template>

<script>
import Clipboard from "clipboard";
import { getOrders, cancelOrder, rePay, inviteSteamFriend, getGift, uploadShowCdkeyTime } from "@/api/my.js";

export default {
  name: "Order",
  data() {
    return {
      orders: [],
      payFormPayData: {},
      howto_modal: false
    };
  },
  watch: {
    payFormPayData() {
      this.$nextTick(function() {
        // console.log("UI渲染完成");
        document.getElementById("payForm").submit();
      });
    }
  },
  created() {
    this.getOrders();
  },
  methods: {
    addFriend(game) {
      const user_steam_id = game.user_steam_id;
      if (user_steam_id == "" || user_steam_id == undefined || user_steam_id == null) {
        this.$Message.error("请输入正确的Steam ID");
        return;
      } else {
        inviteSteamFriend({ "order_content_id": game.id, "user_steam_id": user_steam_id, "gift_id": game.content_id }).then(res => {
          game.steam_friend_relation = 1;
          this.$Message.success("邀请成功");
        }).catch(err => {
          this.$Message.error(err.msg);
        });
      }
    },
    getGift(game) {
      const user_steam_id = game.user_steam_id;
      getGift({ "order_content_id": game.id, "user_steam_id": user_steam_id, "gift_id": game.content_id }).then(res => {
        this.$Message.success(res.msg);
        game.status = 2;
      }).catch(err => {
        this.$Message.error(err.msg);
        if (err.code == 196) {
          // 该用户已拥有此礼物 清空 user_steam_id
          game.user_steam_id = "";
          game.steam_friend_relation = 0;
        } else if(err.code == 195){
          //还未添加好友
          game.steam_friend_relation = 0;
        }
      });
    },
    rePay(item) {
      this.currentOrderId = item.id;
      rePay({ "order_id": item.id }).then(res => {
        //this.payFormPayData = res.data; //不再使用表单提交的方式
        window.location.href = res.data
      });
    },
    showCdkey(game) {
      if (game.show_cdkey_status == 0) {
        game.show_cdkey = game.cdkey;
        game.redeem_txt = "复制并兑换";
        game.show_cdkey_status = 1;
        //提示用户24小时内进行兑换CDKEY
        if(game.cdkey_show_time == '' || game.cdkey_show_time == undefined || game.cdkey_show_time == null){
          uploadShowCdkeyTime({'order_content_id':game.id}).then(res=>{
            this.$Message.warning('请在24小时内完成兑换，逾期可能无法享受售后权益');
          }).catch(err=>{

          })
        }
      } else {
        const clipboard = new Clipboard(".redeem");
        clipboard.on("success", e => {
          this.$Message.success("复制成功");
          // 释放内存
          clipboard.destroy();
          setTimeout(() => {
            window.open("https://store.steampowered.com/account/registerkey?key=" + game.cdkey);
          }, 1000);
        });
        clipboard.on("error", e => {
          // 不支持复制
          this.$Message.error("该浏览器不支持自动复制");
          // 释放内存
          clipboard.destroy();
        });
      }
    },
    getOrders() {
      getOrders({}).then(res => {
        this.orders = res.data;
      });
    },
    cancelOrder(order_id) {
      cancelOrder({ "id": order_id }).then(res => {
        this.$Message.success(res.msg);
        for (const index in this.orders) {
          if (this.orders[index].id == order_id) {
            this.orders[index].order_status = -1;
          }
        }
      });
    },
    howto() {
      this.howto_modal = true;
    },
    toHome(){
      this.$router.replace({
        path: "/Home"
      });
    }
  }
};
</script>

<style lang="less" scoped>
  .color-cancel{
    color:#ed4014
  }
  .color-suspend{
    color:#ff6a00
  }
  .color-success{
    color:#19be6b
  }

  .order {
    max-width: 1600px;
    margin: 0 auto;
    .title {
      color: @warn;
      font-size: 16px;
      padding: 30px 0 24px 20px;
    }
    .content {
      padding: 30px 20px;
      background: rgba(255, 255, 255, 0.05);
      color: #ccc;
      .order {
        padding: 30px;
        background: rgba(255, 255, 255, 0.1);
        margin-bottom: 30px;
        &:last-child {
          margin-bottom: 0;
        }
        .header {
          display: flex;
          justify-content: space-between;
          border-bottom: 1px dashed #808081;
          padding-bottom: 14px;
          .status,.money {
            span {
              color: @warn;
              margin-left: 5px;
            }
            .color-cancel{
              color:#ed4014
            }
            .color-suspend{
              color:#ff6a00
            }
            .color-success{
              color:#19be6b
            }
          }
        }

        .container{
          display: flex;
          justify-content: space-between;
          align-items: center;
        }
      }
    }

    .list{
      width:100%;
      .cell{
        display: flex;
        margin: 25px 0px;
        .center{
          display: flex;
          flex-direction: column;
          justify-content: space-around
        }
        .right{
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: flex-end;
          .tips{
            width:280px;
            height:48px;
            line-height: 48px;
            background: #ff6a00;
            color:#fff;
            border-radius: 5px;
            text-align: center;
            font-size: 18px;
            cursor: pointer;
          }

          .howto{
            font-size: 14px;
            margin-top: 8px;
          }

          .show{
            width:280px;
            height:48px;
            line-height: 48px;
            background: #ff6a00;
            color:#fff;
            border-radius: 5px;
            display: flex;
            align-items: center;
            .cdkeycon, .giftcon{
              max-width: 150px;
              padding-left: 10px;
              color:#fff;
              background: #33303d;
              height: 46px;
              margin-left: 1px;
              border-radius: 5px 0 0 5px;
            }
            .giftcon{
              color:#ccc;
              padding-left: 10px;
              font-size: 14px;
            }
            .giftcon::placeholder{
              color:#ccc;
              font-size: 10px;
            }
            .redeem,.gift{
              margin-left: 20px;
              cursor: pointer;
            }
          }
        }
        .pic{
        width: 280px;
        height: 155px;
      }
      }
    }
    .left,.right{
      width:280px;
    }

    .center{
      flex-grow: 1;
      padding: 10px 20px;
      .info{
        display: flex;
        align-items: center;
        .district{
          margin:0px 10px;
        }
        .cdkey{
          color:#ff7400;
        }
      }
      .sys{
        display: flex;
        align-items: center;
        .rdm{
          margin-right: 10px;
        }
        .platform{
          height: 22px;
        }
      }
    }
  }

  //重新发起支付的支付方式对话框
  .payway {
    display: flex;
    flex-direction: column;
    padding: 0 20px;
    button {
      //margin-top: 25px;
      margin-top: 10px;
      height: 40px;
      /deep/ span {
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 16px;
      }
      &:last-child {
        margin-bottom: 10px;
      }
    }
    .paypal {
      background: @warn;
      border-color: @warn;
      .paypal-icon{
        width: 65px;
        height: 40px;
      }
    }
    .pay-container{
      display: flex;
      justify-content: space-between;
    }
    .alipay {
      background: #2BA6DF;
      border-color: #2BA6DF;
      span {
        margin-left: 6px;
      }
      width: 140px;
      .payForm{
        display: none;
      }
    }
    .weixin {
      background: #28c445;
      border-color: #28c445;
      span {
        margin-left: 6px;
      }
      width: 140px;
    }
  }

  .toHome{
    color:#ff7400;
  }
</style>
