import request from "@/utils/request";

/**
 * 获取用户订单数据
 */
 export function getOrders(data) {
  return request({
    url: "/my/orders",
    method: "post",
    data
  });
}

/**
 * 重新支付获取订单参数
 */
 export function rePay(data) {
  return request({
    url: "/cart/repay",
    method: "post",
    data
  });
}

